.container {
  width: 100%;
  padding-right: 2rem;
  padding-left: 2rem;
}

@media (min-width: 640px) {
  .container {
    max-width: 640px;
    padding-right: 4rem;
    padding-left: 4rem;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding-right: 8rem;
    padding-left: 8rem;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-right: 16rem;
    padding-left: 16rem;
  }
}

#root{
  background:#111;
  --text-opacity: 1;
  color: #f7fafc;
  color: rgba(247, 250, 252, var(--text-opacity));
  min-height:100vh
}

.text-theme{
  color:#fed204
}

a{
  text-decoration:underline
}



#header h1{
  font-size:2.5rem
}

#header a{
  text-decoration:none
}

.page-main h2{
  font-size:1.5rem;
  margin-top:0.5rem
}

.page-main p,.page-main ul{
  padding-bottom:1.5rem
}

.page-main .ecw{
  color:#EB7F56;
  font-weight:bold
}

.page-letters h2{
  font-size:1.5rem;
  font-weight:bold;
  padding-top:1rem;
  padding-bottom:0.5rem
}

.page-letters .city{
  display:inline-block;
  width:16rem
}

.page-letters .link{
  display:inline-block;
  width:8rem
}

.page-letters .location input{
  background:#333
}

.bg-black {
  --bg-opacity: 1;
  background-color: #000;
  background-color: rgba(0, 0, 0, var(--bg-opacity))
}

.flex {
  display: flex
}

.flex-col {
  flex-direction: column
}

.items-center {
  align-items: center
}

.justify-center {
  justify-content: center
}

.text-2xl {
  font-size: 1.5rem
}

.list-outside {
  list-style-position: outside
}

.list-disc {
  list-style-type: disc
}

.mx-auto {
  margin-left: auto;
  margin-right: auto
}

.ml-4 {
  margin-left: 1rem
}

.p-1 {
  padding: 0.25rem
}

.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem
}

.py-4 {
  padding-top: 1rem;
  padding-bottom: 1rem
}

.py-10 {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem
}

.pt-4 {
  padding-top: 1rem
}

.pb-4 {
  padding-bottom: 1rem
}

.pt-5 {
  padding-top: 1.25rem
}

.pl-5 {
  padding-left: 1.25rem
}

.text-center {
  text-align: center
}

@media (min-width: 768px) {

  .md\:flex-row {
    flex-direction: row
  }

  .md\:justify-between {
    justify-content: space-between
  }
}

